import * as React from "react";
import { UndoAction } from "./undo";

interface Props {
    past: any[];
    future: any[];
    dispatch: React.Dispatch<UndoAction>;
}

const makeListener = (props: Props) => (event: KeyboardEvent) => {
    if (!event.ctrlKey && !event.metaKey) return;
    if (event.target instanceof HTMLInputElement) return;
    if (event.target instanceof HTMLTextAreaElement) return;
    switch (event.key) {
        case "z": {
            if (props.past.length === 0) return;
            return props.dispatch({ type: "undo" });
        }
        case "Z":
        case "y": {
            if (props.future.length === 0) return;
            return props.dispatch({ type: "redo" });
        }
    }
};

export const useDiagramUndoShortcut = (props: Props) => {
    const { past, future, dispatch } = props;
    React.useEffect(() => {
        const listener = makeListener({ past, future, dispatch });
        document.addEventListener("keyup", listener);
        return () => document.removeEventListener("keyup", listener);
    }, [past, future, dispatch]);
};
