import { Button, ButtonGroup, Select } from "@moai/core";
import { icons } from "@moai/icon";

import React from "react";
import { HeaderChildProps } from "./utils/size";

interface Props extends HeaderChildProps {  
    viewportRef: React.RefObject<HTMLDivElement>;
    zoom: number;
    setZoom: React.Dispatch<React.SetStateAction<number>>;
}

export const HeaderZoom = (props: Props) => (
    <ButtonGroup>
        {[
            <Button
                key="out"
                icon={icons.zoomOut}
                onClick={() => props.setZoom(props.zoom - 0.25)}
                size={props.size.button}
            />,
            <Select
                key="select"
                value={props.zoom}
                setValue={props.setZoom}
                options={[25, 50, 75, 100, 125, 150, 175, 200].map((value) => {
                    return { label: `${value}%`, value: value / 100 };
                })}
                size={props.size.select}
            />,
            <Button
                key="in"
                icon={icons.zoomIn}
                onClick={() => props.setZoom(props.zoom + 0.25)}
                size={props.size.button}
            />,
        ]}
    </ButtonGroup>
);
