import { RelatedEdge } from "app/view/canvas/canvas";
import { EditRow, EditTable } from "diagram/edit/edit";
import * as React from "react";
import { EdgeFromRowMap, getTableRowsEdgeMap } from "./edge-map";
import { groupTableRows, TableRowsItem } from "./group";

interface SharedProps {
    table: EditTable;
    getRow: (edgeMap: EdgeFromRowMap, row: EditRow) => JSX.Element;
    getHiddenRows: (item: EditRow[]) => JSX.Element;
    getHideRowsButton: () => JSX.Element | null;
}

interface Props extends SharedProps {
    relatedEdges: RelatedEdge[];
}

/** Props of each Item, which could be a row or a group of rows */
interface ItemProps extends SharedProps {
    edgeMap: EdgeFromRowMap;
    item: TableRowsItem;
}

const Item = (props: ItemProps) => {
    // Single row
    if (!Array.isArray(props.item))
        return props.getRow(props.edgeMap, props.item);
    // Group of rows
    return props.table.isRowsExpand ? (
        <React.Fragment>
            {props.item.map(props.getRow.bind(null, props.edgeMap))}
        </React.Fragment>
    ) : (
        props.getHiddenRows(props.item)
    );
};

export const TableRowsHeadless = (props: Props) => {
    const { rows } = props.table.value;
    const edgeMap = getTableRowsEdgeMap(props.table.id, props.relatedEdges);
    const items = groupTableRows(rows, edgeMap);
    return (
        <>
            {items.map((item) => {
                const key = Array.isArray(item) ? item[0].id : item.id;
                return <Item {...props} {...{ key, edgeMap, item }} />;
            })}
            {props.table.isRowsExpand &&
                items.length !== rows.length &&
                props.getHideRowsButton()}
        </>
    );
};
