import { Border, DivPx } from "@moai/core";
import * as React from "react";
import { usePlatform } from "utils/platform";

export const HeaderBigDiv = (): JSX.Element => {
    const platform = usePlatform();
    switch (platform) {
        case "mac":
            return (
                <>
                    <DivPx size={16} />
                    <DivPx size={16} />
                </>
            );
        default:
            return (
                <>
                    <DivPx size={16} />
                    <Border color="weak" />
                    <DivPx size={16} />
                </>
            );
    }
};
