const prototype = CSSStyleDeclaration.prototype;

// Safari has a bug where getPropertyValue won't return the "#" inside the url
// path. See https://github.com/tsayen/dom-to-image/issues/125#issuecomment-672762287
export const fixSafari = () => {
    // DomToImage uses cssText first, but we can't override this method so we
    // need to disable it
    Object.defineProperty(prototype, "cssText", { value: "" });

    // DomToImage will then fallback to getPropertyValue, which we will
    // override, so this is a backup version
    Object.defineProperty(prototype, "getPropertyValueOriginal", {
        value: prototype.getPropertyValue,
    });

    // Now we override the getPropertyValue method to ensure the "#" is there
    Object.defineProperty(prototype, "getPropertyValue", {
        value: function (name: string) {
            const value: string = (this as any).getPropertyValueOriginal(name);
            if (name === "marker-start" && value.endsWith(`edge-from")`))
                return "url(#edge-from)";
            if (name === "marker-end" && value.endsWith(`edge-to")`))
                return "url(#edge-to)";
            return value;
        },
    });
};
