import { Background, Border, Button } from "@moai/core";
import { DiagramAction } from "diagram/state/reducer/reducer";
import * as React from "react";

interface RowsProps {
    count: number;
    tableId: number;
    diagramDispatch: React.Dispatch<DiagramAction>;
}

export const TableHiddenRows = (props: RowsProps) => (
    <Background color="secondary">
        <Border color="weak" />
        <div style={{ marginTop: -1 }} />
        <Button
            style={Button.style.flat}
            size={Button.size.small}
            isFullWidth
            onClick={() => {
                props.diagramDispatch({
                    type: "set-table-rows-expand",
                    payload: { id: props.tableId, isExpand: true },
                });
            }}
            children={`${props.count} columns hidden`}
        />
        <div style={{ marginTop: -1 }} />
        <Border color="weak" />
    </Background>
);

interface ButtonProps {
    tableId: number;
    diagramDispatch: React.Dispatch<DiagramAction>;
}

export const TableHideRowsButton = (props: ButtonProps) => (
    <Button
        style={Button.style.flat}
        size={Button.size.small}
        isFullWidth
        onClick={() => {
            props.diagramDispatch({
                type: "set-table-rows-expand",
                payload: { id: props.tableId, isExpand: false },
            });
        }}
        children="Hide columns"
    />
);
