import { Button, ButtonGroup, ButtonMenu, ButtonMenuAction } from "@moai/core";
import { icons } from "@moai/icon";
import { DiagramAction } from "diagram/state/reducer/reducer";
import React from "react";
import { HeaderChildProps } from "../utils/size";

interface Props extends HeaderChildProps {
    diagramDispatch: React.Dispatch<DiagramAction>;
}

const layoutAction: DiagramAction = {
    type: "set-layout",
    payload: { type: "auto" },
};

const setExpandAndArrange = (props: Props, payload: boolean): void => {
    // @TODO: Should not fire 2 dispatch like this
    props.diagramDispatch({ type: "set-all-tables-rows-expand", payload });
    window.setTimeout(() => props.diagramDispatch(layoutAction), 0);
};

const getActions = (props: Props): ButtonMenuAction[] => [
    {
        label: "Expand all columns and Arrange",
        fn: () => setExpandAndArrange(props, true),
    },
    {
        label: "Collapse columns and Arrange",
        fn: () => setExpandAndArrange(props, false),
    },
];

export const HeaderLayout = (props: Props) => (
    <ButtonGroup>
        {[
            <Button
                key="arrange"
                icon={icons.layoutAuto}
                onClick={() => void props.diagramDispatch(layoutAction)}
                children="Arrange"
                size={props.size.button}
            />,
            <ButtonMenu
                key="more"
                actions={getActions(props)}
                icon={icons.caret}
                size={props.size.button}
            />,
        ]}
    </ButtonGroup>
);
