import { Button, ButtonGroup } from "@moai/core";
import { icons } from "@moai/icon";
import { UndoAction } from "diagram/state/undo/undo";
import React from "react";
import { HeaderChildProps } from "./utils/size";

interface Props<T> extends HeaderChildProps {
    past: T[];
    future: T[];
    dispatch: React.Dispatch<UndoAction>;
}

export const HeaderHistory = <T,>(props: Props<T>) => (
    <ButtonGroup>
        {[
            <Button
                key="undo"
                onClick={() => props.dispatch({ type: "undo" })}
                icon={icons.undo}
                size={props.size.button}
                disabled={props.past.length === 0}
            />,
            <Button
                key="redo"
                onClick={() => props.dispatch({ type: "redo" })}
                icon={icons.redo}
                size={props.size.button}
                disabled={props.future.length === 0}
            />,
        ]}
    </ButtonGroup>
);
