import { makeDiagramFromEdit } from "diagram/edit/convert/from";
import { EditDiagram } from "diagram/edit/edit";
import { Platform } from "utils/platform";

export const exportSqlDrivers = [
    "MySQL",
    "MariaDB",
    "SQLite",
    "PostgreSQL",
    "Cassandra",
    "Snowflake",
    "MicrosoftSQLServer",
];

interface Props {
    diagram: EditDiagram;
    driver: string;
    platform: Platform;
}

const METHOD_NAME = "exportSQL";

export const exportAsSql = (props: Props): void => {
    const body = {
        data: makeDiagramFromEdit(props.diagram),
        driver: props.driver,
    };
    switch (props.platform) {
        case "web":
            return window.alert("Export to SQL requires TablePlus app");
        case "mac":
            const handlers = (window as any).webkit.messageHandlers;
            handlers[METHOD_NAME].postMessage(body);
            return;
        case "win":
            const message = { message: METHOD_NAME, args: body };
            (window as any).chrome.webview.postMessage(message);
            return;
    }
};
