import { DiagramReducer } from "../reducer";
import { addTable, AddTablePayload } from "./add";
import { setTablePosition, SetTablePositionPayload } from "./position";
import { removeMultipleTables, RemoveMultipleTablesPayload, removeTable, RemoveTablePayload } from "./remove"; // prettier-ignore
import { updateTableName, UpdateTableNamePayload } from "./update-name";
import { SetTableRowsExpandPayload, SetAllTablesRowsExpandPayload, setTableRowsExpand, setAllTablesRowsExpand } from "./row"; // prettier-ignore

export type DiagramTableActions =
    | { type: "remove-table"; payload: RemoveTablePayload }
    | { type: "remove-multiple-tables"; payload: RemoveMultipleTablesPayload }
    | { type: "add-table"; payload: AddTablePayload }
    | { type: "set-table-position"; payload: SetTablePositionPayload }
    | { type: "set-table-rows-expand"; payload: SetTableRowsExpandPayload }
    | { type: "set-all-tables-rows-expand"; payload: SetAllTablesRowsExpandPayload } // prettier-ignore
    | { type: "update-table-name"; payload: UpdateTableNamePayload };

export const diagramTableReducer: DiagramReducer = (diagram, action) => {
    switch (action.type) {
        case "add-table":
            return addTable(diagram, action.payload);
        case "remove-table":
            return removeTable(diagram, action.payload);
        case "remove-multiple-tables":
            return removeMultipleTables(diagram, action.payload);
        case "update-table-name":
            return updateTableName(diagram, action.payload);
        case "set-table-position":
            return setTablePosition(diagram, action.payload);
        case "set-table-rows-expand":
            return setTableRowsExpand(diagram, action.payload);
        case "set-all-tables-rows-expand":
            return setAllTablesRowsExpand(diagram, action.payload);
        default:
            return diagram;
    }
};
