import { ButtonMenu } from "@moai/core";
import { DiagramAction } from "diagram/state/reducer/reducer";
import * as React from "react";
import { icons } from "@moai/icon";
import { HeaderChildProps } from "../utils/size";
import { ImportRawPayload } from "diagram/state/reducer/import/raw";

interface Props extends HeaderChildProps {
    diagramDispatch: React.Dispatch<DiagramAction>;
}

const selfSamples = "https://diagramplus.com/samples";

const loadRemote = async (props: Props, url: string) => {
    const raw = await fetch(url).then((response) => response.json());
    const payload: ImportRawPayload = { raw, isAppend: false };
    props.diagramDispatch({ type: "import-raw", payload });
};

export const HeaderImport = (props: Props) => (
    <ButtonMenu
        icon={icons.import}
        children="Import"
        actions={[
            {
                label: "Import from Database (require TablePlus app)",
                disabled: true,
                fn: () => {},
            },
            { label: "", fn: () => {}, disabled: true },
            {
                label: "Load sample 1 (3 small tables)",
                fn: () => loadRemote(props, `${selfSamples}/1.json`),
            },
            {
                label: "Load sample 2 (Table with many columns)",
                fn: () => loadRemote(props, `${selfSamples}/2.json`),
            },
            {
                label: "Load sample 3 (28 tables)",
                fn: () => loadRemote(props, `${selfSamples}/3.json`),
            },
            {
                label: "Load sample 4 (1710 tables)",
                fn: () => loadRemote(props, `${selfSamples}/4.json`),
            },
        ]}
        size={props.size.button}
    />
);
