import * as React from "react";
import { Checkbox, Input, Paragraph, MutedDiv } from "@moai/core";
import s from "./item.module.css";

export interface ReportItemState {
    has: boolean;
    setHas: React.Dispatch<React.SetStateAction<boolean>>;
    text: string;
    setText: React.Dispatch<React.SetStateAction<string>>;
}

interface Props {
    label: string;
    state: ReportItemState;
    placeholder?: string;
    children?: React.ReactNode;
}

export const useReportItem = (getInitial: () => string): ReportItemState => {
    const [has, setHas] = React.useState(true);
    const [text, setText] = React.useState(getInitial);
    return { has, setHas, text, setText };
};

export const ReportItem = (props: Props) => (
    <div className={s.container}>
        <div className={s.input}>
            <div className={s.has}>
                <Checkbox
                    value={props.state.has}
                    setValue={props.state.setHas}
                    children={props.label}
                />
            </div>
            <div className={s.text}>
                <Input
                    value={props.state.text}
                    setValue={props.state.setText}
                    disabled={!props.state.has}
                    placeholder={props.placeholder}
                />
            </div>
        </div>
        {props.children && (
            <MutedDiv>
                <Paragraph>{props.children}</Paragraph>
            </MutedDiv>
        )}
    </div>
);
