import { ButtonMenu, ButtonMenuAction } from "@moai/core";
import { icons } from "@moai/icon";
import { EditDiagram } from "diagram/edit/edit";
import React from "react";
import { exportAsImage } from "./image/image";
import { exportAsSql, exportSqlDrivers } from "./sql/sql";
import { HeaderChildProps } from "../utils/size";
import { usePlatform } from "utils/platform";

interface Props extends HeaderChildProps {
    diagram: EditDiagram;
    setIsCapture: React.Dispatch<React.SetStateAction<boolean>>;
}

export const HeaderExport = (props: Props) => {
    const { diagram, setIsCapture } = props;
    const platform = usePlatform();
    const actions: ButtonMenuAction[] = [
        {
            label: "Save as Image (PNG)",
            fn: () => exportAsImage({ setIsCapture, platform, format: "png" }),
        },
        {
            label: "Save as Web page (SVG)",
            fn: () => exportAsImage({ setIsCapture, platform, format: "svg" }),
        },
        { label: "", fn: () => {}, disabled: true },
        ...exportSqlDrivers.map((driver) => ({
            label: `Export to ${driver}`,
            fn: () => exportAsSql({ diagram, driver, platform }),
        })),
    ];
    return (
        <ButtonMenu
            icon={icons.share}
            actions={actions}
            size={props.size.button}
        />
    );
};
