import {
    background,
    Border,
    borderColor,
    Button,
    DivPx,
    TextArea,
} from "@moai/core";
import { EDIT_DIAGRAM_STORAGE_KEY } from "diagram/state/storage/storage";
import * as React from "react";
import { globalLogger } from "utils/external";
import { usePlatform } from "utils/platform";
import { ReportItem, ReportItemState, useReportItem } from "./item/item";
import s from "./report.module.scss";

export type ReportType = "uncaught" | "manual";

interface Props {
    type: ReportType;
    title: string;
    description: React.ReactNode;
    footer?: React.ReactNode;
    error?: string;
    onDone?: () => void;
    onClickOutside?: () => void;
}

const getDiagram = (): string =>
    window.localStorage.getItem(EDIT_DIAGRAM_STORAGE_KEY) ?? "None";

export interface ReportBody {
    title: string;
    platform: string;
    // Optional info
    error?: string;
    message?: string;
    contact?: string;
    // Files
    diagram?: string;
    log?: string;
}

const getItem = (state: ReportItemState): string | undefined =>
    state.has ? state.text : undefined;

export const ErrorReport = (props: Props) => {
    const isUncaught = props.type === "uncaught";

    const message = useReportItem(() => "");
    const contact = useReportItem(() => "");
    const error = useReportItem(() => props.error ?? "");
    const log = useReportItem(() => JSON.stringify(globalLogger));
    const diagram = useReportItem(() => getDiagram());
    const platform = usePlatform();

    const [sending, setSending] = React.useState(false);

    const report = async () => {
        setSending(true);
        const body: ReportBody = {
            title: isUncaught ? "Uncaught error" : "Manual report",
            platform,
            // Texts
            error: getItem(error),
            message: getItem(message),
            contact: getItem(contact),
            // Files
            diagram: getItem(diagram),
            log: getItem(log),
        };
        await window.fetch("https://diagramplus.com/api/report", {
            method: "POST",
            body: JSON.stringify(body),
            headers: { "Content-type": "application/json" },
        });
        window.alert("The report has been sent successfully");
        setSending(false);
        props.onDone?.();
    };

    const body = (
        <>
            <h1 className={s.title}>{props.title}</h1>
            <DivPx size={16} />
            <Border color="weak" />
            <DivPx size={16} />
            {props.description}
            <TextArea
                autoFocus
                rows={3}
                value={message.text}
                setValue={message.setText}
            />
            <DivPx size={16} />
            {isUncaught && (
                <>
                    <ReportItem label="Include error detail" state={error} />
                    <DivPx size={8} />
                </>
            )}
            <ReportItem label="Include the diagram" state={diagram} />
            <DivPx size={8} />
            <ReportItem label="Include application log" state={log} />
            <DivPx size={8} />
            <ReportItem
                state={contact}
                label="Allow us to contact you at:"
                placeholder="name@example.com"
            />
            <DivPx size={16} />
            <Border color="weak" />
            <DivPx size={16} />
            <div className={s.footer}>
                {props.footer}
                <DivPx size={16} />
                <Button
                    disabled={sending}
                    highlight
                    onClick={report}
                    children={sending ? "Sending report" : "Send Report"}
                />
            </div>
        </>
    );

    return (
        <div className={s.container}>
            <div className={[s.backdrop, background.secondary].join(" ")} />
            <div
                className={s.main}
                onClick={(event) => {
                    if (event.target !== event.currentTarget) return;
                    props.onClickOutside?.();
                }}
            >
                <div
                    className={[
                        s.body,
                        borderColor.strong,
                        background.primary,
                    ].join(" ")}
                    children={body}
                />
            </div>
        </div>
    );
};
