import { Diagram } from "diagram/diagram";
import { makeEditFromDiagram } from "diagram/edit/convert/to";
import { validateSchema } from "diagram/schema/schema";
import { validateSemanticMutable } from "diagram/schema/semantic";
import { DiagramReducer } from "../reducer";
import { appendDiagram } from "./append";
import { ImportPayload } from "./import";

export interface ImportRawPayload extends ImportPayload {
    raw: object;
}

type Payload = ImportRawPayload;

export const importRaw: DiagramReducer<Payload> = (prev, payload) => {
    const raw = payload.raw as Diagram;
    // This throws error if the payload is not valid (e.g. typo). For now we
    // will just let it goes up to trigger the global error catcher since it's
    // not recoverable
    validateSchema(raw);
    validateSemanticMutable(raw); // This may MUTATE the diagram
    const next = makeEditFromDiagram(raw);
    if (!payload.isAppend) return next;
    return appendDiagram(prev, next);
};
