import { getExportImageFile } from "./get";
import { saveExportImageFile } from "./save";
import { Platform } from "utils/platform";

export type ExportImageFormat = "png" | "svg";

export interface ExportImageFile {
    prefix: string;
    body: string; // base64, without prefix
}

interface Props {
    setIsCapture: React.Dispatch<React.SetStateAction<boolean>>;
    format: ExportImageFormat;
    platform: Platform;
}

export const exportAsImage = async (props: Props): Promise<void> => {
    const { setIsCapture, format, platform } = props;
    const canvas = document.getElementById("canvas");
    if (canvas === null) throw Error("Canvas is null");
    const file = await getExportImageFile({ canvas, format, setIsCapture });
    saveExportImageFile({ file, format, platform });
};
