import { EditDiagram } from "diagram/edit/edit";
import * as React from "react";
import { usePlatform } from "utils/platform";
import { emptyDiagram, fallbackDiagram } from "./fallback";

export const EDIT_DIAGRAM_STORAGE_KEY = "diagram";

const parseErrorMessage = [
    "There was an error while parsing the locally saved diagram. The app will ",
    "now fallback to a sample diagram.\n\n",
    "Your previous diagram is still available in your localStorage under the ",
    `key "${EDIT_DIAGRAM_STORAGE_KEY}-invalid". Please contact us if you need help `,
    "retrieving it.",
].join("");

const getDiagram = (): EditDiagram => {
    const stored = window.localStorage.getItem(EDIT_DIAGRAM_STORAGE_KEY);
    if (stored === null) return fallbackDiagram;
    try {
        return JSON.parse(stored);
    } catch {
        window.localStorage.setItem(
            `${EDIT_DIAGRAM_STORAGE_KEY}-invalid`,
            stored
        );
        window.alert(parseErrorMessage);
        return fallbackDiagram;
    }
};

const setDiagram = (diagram: EditDiagram) => {
    window.localStorage.setItem(
        EDIT_DIAGRAM_STORAGE_KEY,
        JSON.stringify(diagram)
    );
};

interface DiagramStorageState {
    get: () => EditDiagram;
    set: (diagram: EditDiagram) => void;
}

export const useDiagramStorage = (): DiagramStorageState => {
    const platform = usePlatform();
    const storage = React.useMemo(() => {
        return platform === "web"
            ? {
                  get: () => getDiagram(),
                  set: (diagram: EditDiagram) => setDiagram(diagram),
              }
            : {
                  get: () => emptyDiagram,
                  // Still set diagram for error reporting purpose
                  set: (diagram: EditDiagram) => setDiagram(diagram),
              };
    }, [platform]);
    return storage;
};
