import { DiagramReducer } from "../reducer";
import { importRaw, ImportRawPayload } from "./raw";

export interface ImportPayload {
    isAppend: boolean;
}
export interface ImportJsonPayload extends ImportPayload {
    text: string;
}
export interface ImportBase64Payload extends ImportPayload {
    text: string;
}

export type DiagramImportActions =
    | { type: "import-raw"; payload: ImportRawPayload }
    | { type: "import-json"; payload: ImportPayload & { text: string } }
    | { type: "import-base64"; payload: ImportPayload & { text: string } };

const importJson: DiagramReducer<ImportJsonPayload> = (diagram, payload) => {
    const raw = JSON.parse(payload.text);
    return importRaw(diagram, { ...payload, raw });
};

type Base64Reducer = DiagramReducer<ImportBase64Payload>;
const importBase64: Base64Reducer = (diagram, payload) => {
    const decoder = new TextDecoder();
    const bytes = Uint8Array.from(atob(payload.text), (c) => c.charCodeAt(0));
    const text = decoder.decode(bytes);
    return importJson(diagram, { ...payload, text });
};

export const diagramImportReducer: DiagramReducer = (diagram, action) => {
    switch (action.type) {
        case "import-raw":
            return importRaw(diagram, action.payload);
        case "import-json":
            return importJson(diagram, action.payload);
        case "import-base64":
            return importBase64(diagram, action.payload);
        default:
            return diagram;
    }
};
