import * as Moai from "@moai/core";
import { Platform } from "utils/platform";

export interface HeaderSize {
    button: Moai.ButtonSize;
    input: Moai.InputSize;
    select: Moai.SelectSize;
}

export interface HeaderChildProps {
    size: HeaderSize;
}

export const getHeaderSize = (platform: Platform): HeaderSize => {
    switch (platform) {
        case "mac":
            return {
                button: Moai.Button.size.small,
                input: Moai.Input.size.small,
                select: Moai.Select.size.small,
            };
        default:
            return {
                button: Moai.Button.size.medium,
                input: Moai.Input.size.medium,
                select: Moai.Select.size.medium,
            };
    }
};
