import { Diagram, Item, RefSide } from "../diagram";

// ReferenceError of reference... sorry for the naming
class DiagramRefError extends Error {
    constructor(message: string) {
        super(message);
        this.name = "DiagramRefError";
    }
}

const refError = (name: string, target: string) => {
    return new DiagramRefError(`${name} has an invalid reference to ${target}`);
};

const isSameSchema = (diagram: Diagram, side: RefSide, item: Item): boolean => {
    const a = side.schema ?? diagram.schema;
    const b = item.schema ?? diagram.schema;
    return a === b;
};

const validateRefSide = (diagram: Diagram, side: RefSide, index: number) => {
    const table = diagram.items.find((item) => {
        return item.name === side.name && isSameSchema(diagram, side, item);
    });
    if (!table) throw refError(`Ref #${index}`, `table "${side.name}"`);
    side.rows.forEach((rowId) => {
        if (table.rows.some((row) => row.id === rowId)) return;
        throw refError(`Ref #${index}.rows`, `row "${rowId}"`);
    });
};

const validateRowsIds = (item: Item) => {
    const ids = item.rows.map((row) => row.id);
    if (new Set(ids).size === ids.length) return;
    throw Error(`tables.${item.id}.rows.ids are not unique`);
};

const validateItemsIds = (diagram: Diagram) => {
    const ids = diagram.items.map((item) => item.id);
    if (new Set(ids).size === ids.length) return;
    throw Error("items.ids are not unique");
};

/**
 * Mutable because it also tries to fix recoverable issues like ref with
 * invalid references
 */
export const validateSemanticMutable = (diagram: Diagram): void => {
    // tables
    validateItemsIds(diagram);
    diagram.items.forEach((item) => {
        validateRowsIds(item);
    });
    // refs
    diagram.refs = diagram.refs.filter((ref, index) => {
        try {
            validateRefSide(diagram, ref.from, index);
            validateRefSide(diagram, ref.to, index);
        } catch (error) {
            // When a ref has invalid references, we simply remove it instead
            // of throwing error
            if (error instanceof DiagramRefError) {
                console.warn(error);
                return false;
            }
            throw error;
        }
        return true;
    });
};
