import { DiagramReducer } from "../reducer";

export interface SetTableRowsExpandPayload {
    id: number;
    isExpand: boolean;
}

export type SetAllTablesRowsExpandPayload = boolean;

type Reducer = DiagramReducer<SetTableRowsExpandPayload>;
export const setTableRowsExpand: Reducer = (diagram, payload) => {
    const tables = diagram.tables.map((table) => {
        if (table.id !== payload.id) return table;
        return { ...table, isRowsExpand: payload.isExpand };
    });
    return { ...diagram, tables };
};

type AllReducer = DiagramReducer<SetAllTablesRowsExpandPayload>;
export const setAllTablesRowsExpand: AllReducer = (diagram, payload) => {
    const tables = diagram.tables.map((table) => {
        return { ...table, isRowsExpand: payload };
    });
    return { ...diagram, tables };
};
