import { getRowElementId } from "app/row/dom";
import { Row } from "app/row/row";
import { EdgeFromRowMap } from "app/table/rows/edge-map";
import { TableRowsHeadless } from "app/table/rows/headless";
import { RelatedEdge } from "app/view/canvas/canvas";
import { EditColumn, EditRow, EditTable } from "diagram/edit/edit";
import { DiagramAction } from "diagram/state/reducer/reducer";
import * as React from "react";
import { TableHiddenRows, TableHideRowsButton } from "./hidden";

/** Shared props between a single row and a list of rows */
interface SharedProps {
    // Table
    table: EditTable;
    isEdit: boolean;
    isColumnsExpand: boolean;
    // Diagram
    columns: EditColumn[];
    relatedEdges: RelatedEdge[];
    diagramDispatch: React.Dispatch<DiagramAction>;
}

/** Props of this component */
interface Props extends SharedProps {}

/** Props of each RowContainer */
interface RowContainerProps extends SharedProps {
    edgeMap: EdgeFromRowMap;
    row: EditRow;
}

const RowContainer = (props: RowContainerProps) => (
    <div
        className="table-row" // For keyboard navigation
        id={getRowElementId(props.table.id, props.row.id)}
    >
        <Row
            // row
            row={props.row}
            edge={props.edgeMap.get(props.row.id) ?? null}
            // table
            tableId={props.table.id}
            isTableEdit={props.isEdit}
            isTableColumnsExpand={props.isColumnsExpand}
            // diagram
            columns={props.columns}
            diagramDispatch={props.diagramDispatch}
        />
    </div>
);

export const TableBodyRows = (props: Props) => (
    <TableRowsHeadless
        // Data
        table={props.table}
        relatedEdges={props.relatedEdges}
        // Components
        getHideRowsButton={() => (
            <TableHideRowsButton
                tableId={props.table.id}
                diagramDispatch={props.diagramDispatch}
            />
        )}
        getHiddenRows={(item) => (
            <TableHiddenRows
                count={item.length}
                tableId={props.table.id}
                diagramDispatch={props.diagramDispatch}
            />
        )}
        getRow={(edgeMap, row) => (
            <RowContainer {...props} row={row} edgeMap={edgeMap} />
        )}
    />
);
