import { Switcher, SwitcherOption } from "@moai/core";
import { icons } from "@moai/icon";
import { Theme } from "app/view/theme/theme";
import React from "react";
import { HeaderChildProps } from "../utils/size";

const options: SwitcherOption<Theme>[] = [
    { value: "theme-light", icon: icons.flash, key: "light" },
    { value: "theme-auto", label: "System", key: "auto" },
    { value: "theme-dark", icon: icons.moon, key: "dark" },
];

interface Props extends HeaderChildProps {
    theme: Theme;
    setTheme: React.Dispatch<React.SetStateAction<Theme>>;
}

export const HeaderTheme = (props: Props) => (
    <Switcher<Theme>
        value={props.theme}
        setValue={props.setTheme}
        options={options}
        size={props.size.button}
    />
);
