import { makeDiagramFromEdit } from "diagram/edit/convert/from";
import React from "react";
import { addExternalFn, addExternalFnDirect } from "utils/external";
import { EditDiagram } from "../../edit/edit";
import {
    ImportBase64Payload,
    ImportJsonPayload,
} from "../reducer/import/import";
import { ImportRawPayload } from "../reducer/import/raw";
import { DiagramAction } from "../reducer/reducer";

const registerLegacy = addExternalFn("importDiagram");
const registerJson = addExternalFn("importDiagramJSON");
const registerObject = addExternalFn("importDiagramObject");
const registerBase64 = addExternalFn("importDiagramBase64");

export const useDiagramExternal = (
    diagram: EditDiagram,
    diagramDispatch: React.Dispatch<DiagramAction>
) => {
    React.useEffect(() => {
        registerLegacy((text: string) => {
            const payload: ImportJsonPayload = { text, isAppend: false };
            diagramDispatch({ type: "import-json", payload });
        });
        registerJson((text: string) => {
            const payload: ImportJsonPayload = { text, isAppend: false };
            diagramDispatch({ type: "import-json", payload });
        });
        registerObject((raw: object) => {
            const payload: ImportRawPayload = { raw, isAppend: false };
            diagramDispatch({ type: "import-raw", payload });
        });
        registerBase64((text: string) => {
            const payload: ImportBase64Payload = { text, isAppend: false };
            diagramDispatch({ type: "import-base64", payload });
        });
    }, [diagramDispatch]);

    React.useEffect(() => {
        addExternalFnDirect("exportDiagram", () => {
            return JSON.stringify(makeDiagramFromEdit(diagram));
        });
    }, [diagram]);
};
