import { background, Border, borderColor } from "@moai/core";
import { getRowElementId } from "app/row/dom";
import { RelatedEdge } from "app/view/canvas/canvas";
import { EditColumn, EditRow, EditTable } from "diagram/edit/edit";
import * as React from "react";
import { TableRowsHeadless } from "../rows/headless";
import { TABLE_CLIENT_WIDTH } from "../utils";

interface SharedProps {
    table: EditTable;
    columns: EditColumn[];
}

interface Props extends SharedProps {
    relatedEdges: RelatedEdge[];
}

interface RowProps extends SharedProps {
    row: EditRow;
}

const Field = (props: RowProps & { index: number }) => (
    <div
        style={{
            flex: "1 1 0px",
            overflow: "hidden",
            textAlign: props.index === 1 ? "right" : undefined,
        }}
    >
        {props.row.value[props.columns[props.index].value.name] ?? "<empty>"}
    </div>
);

const rowStyle: React.CSSProperties = {
    display: "flex",
    padding: "0 16px",
};

const Row = (props: RowProps) => (
    <div style={rowStyle} id={getRowElementId(props.table.id, props.row.id)}>
        <Field {...props} index={0} />
        <Field {...props} index={1} />
    </div>
);

const containerProps: React.HTMLAttributes<HTMLDivElement> = {
    className: [background.primary, borderColor.strong].join(" "),
    style: {
        lineHeight: "24px",
        borderStyle: "solid",
        borderWidth: 1,
        width: TABLE_CLIENT_WIDTH,
    },
};

const headerStyle: React.CSSProperties = {
    padding: 8,
    fontWeight: 600,
    textAlign: "center",
};

const HiddenRows = (props: { count: number }) => (
    <div style={{ textAlign: "center" }}>{props.count} columns hidden</div>
);

export const TableInactive = (props: Props) => (
    <div {...containerProps}>
        <div style={headerStyle}>{props.table.value.name}</div>
        <Border color="weak" />
        <div style={{ padding: "8px 0" }}>
            <TableRowsHeadless
                table={props.table}
                relatedEdges={props.relatedEdges}
                getRow={(_edgeMap, row) => <Row {...props} row={row} />}
                getHiddenRows={(item) => <HiddenRows count={item.length} />}
                getHideRowsButton={() => null}
            />
        </div>
    </div>
);
