import { DivPx } from "@moai/core";
import { SetSelectIds } from "app/view/select/select";
import { EditDiagram } from "diagram/edit/edit";
import { DiagramAction } from "diagram/state/reducer/reducer";
import { UndoAction } from "diagram/state/undo/undo";
import React from "react";
import { Platform, usePlatform } from "utils/platform";
import { Theme } from "../theme/theme";
import { HeaderAddTable } from "./add";
import { HeaderExport } from "./export/export";
import { HeaderHistory } from "./history";
import { HeaderImport } from "./import/import";
import { HeaderLayout } from "./layout/layout";
import { HeaderSearch } from "./search";
import s from "./style/header.module.scss";
import sMac from "./style/mac.module.scss";
import sWin from "./style/win.module.scss";
import { HeaderTheme } from "./theme/theme";
import { HeaderBigDiv } from "./utils/div";
import { getHeaderSize } from "./utils/size";
import { HeaderZoom } from "./zoom";

export interface HeaderPrimaryProps {
    // Diagram
    diagram: EditDiagram;
    diagramPast: EditDiagram[];
    diagramFuture: EditDiagram[];
    diagramDispatch: React.Dispatch<DiagramAction | UndoAction>;
    // Theme
    theme: Theme;
    setTheme: React.Dispatch<React.SetStateAction<Theme>>;
    // View
    viewportRef: React.RefObject<HTMLDivElement>;
    zoom: number;
    setZoom: React.Dispatch<React.SetStateAction<number>>;
    setIsCapture: React.Dispatch<React.SetStateAction<boolean>>;
    // Select
    setSelectIds: SetSelectIds;
}

const getStyle = (platform: Platform): string => {
    const cls = [s.container];
    if (platform === "mac") cls.push(sMac.container);
    if (platform === "win") cls.push(sWin.container);
    return cls.join(" ");
};

export const ViewHeaderPure = (props: HeaderPrimaryProps) => {
    const platform = usePlatform();
    const size = getHeaderSize(platform);
    return (
        <div className={[s.container, getStyle(platform)].join(" ")}>
            <div className={s.platformPad} />
            <DivPx size={16} />
            {platform === "win" && (
                <>
                    <HeaderExport
                        diagram={props.diagram}
                        setIsCapture={props.setIsCapture}
                        size={size}
                    />
                    <HeaderBigDiv />
                </>
            )}
            {platform === "web" && (
                <>
                    <HeaderImport
                        diagramDispatch={props.diagramDispatch}
                        size={size}
                    />
                    <HeaderBigDiv />
                </>
            )}
            <HeaderAddTable
                tables={props.diagram.tables}
                diagramDispatch={props.diagramDispatch}
                setSelectIds={props.setSelectIds}
                size={size}
            />
            <DivPx size={16} />
            <HeaderHistory
                past={props.diagramPast}
                future={props.diagramFuture}
                dispatch={props.diagramDispatch}
                size={size}
            />
            <HeaderBigDiv />
            <HeaderSearch
                diagram={props.diagram}
                setSelectIds={props.setSelectIds}
                size={size}
            />
            <HeaderBigDiv />
            <HeaderLayout diagramDispatch={props.diagramDispatch} size={size} />
            <DivPx size={16} />
            <HeaderZoom
                viewportRef={props.viewportRef}
                zoom={props.zoom}
                setZoom={props.setZoom}
                size={size}
            />
            {platform !== "win" && (
                <>
                    <DivPx size={16} />
                    <HeaderExport
                        diagram={props.diagram}
                        setIsCapture={props.setIsCapture}
                        size={size}
                    />
                </>
            )}
            {platform === "web" && (
                <>
                    <HeaderBigDiv />
                    <HeaderTheme
                        theme={props.theme}
                        setTheme={props.setTheme}
                        size={size}
                    />
                    {/* <DivPx size={16} /> */}
                </>
            )}
            {/* <HeaderReport size={size} /> */}
            <DivPx size={16} />
        </div>
    );
};

export const ViewHeader = React.memo(ViewHeaderPure);
