import React from "react";
import { addExternalFnDirect } from "utils/external";
import { Platform, usePlatform } from "utils/platform";

export type Theme = "theme-light" | "theme-dark" | "theme-auto";

let initialTheme: Theme = "theme-light";
addExternalFnDirect("setTheme", (text: string) => {
    initialTheme = (text.includes("theme-") ? text : `theme-${text}`) as Theme;
    document.documentElement.classList.add(initialTheme);
});

const getInitial = (platform: Platform): Theme => {
    switch (platform) {
        case "mac":
            return "theme-auto";
        case "win":
            return initialTheme;
        case "web":
            const stored = localStorage.getItem("theme");
            return (stored as Theme | null) ?? "theme-auto";
        default:
            throw Error("Unknown platform");
    }
};

export const useTheme = () => {
    const platform = usePlatform();
    const [theme, setTheme] = React.useState<Theme>(() => getInitial(platform));

    // Save
    React.useEffect(() => {
        if (platform !== "web") return;
        window.localStorage.setItem("theme", theme);
    }, [theme, platform]);

    // Reset the value we set in load time
    React.useEffect(() => {
        document.documentElement.classList.remove(initialTheme);
    }, []);

    // Apply
    React.useEffect(() => {
        document.documentElement.classList.add(theme);
        return () => document.documentElement.classList.remove(theme);
    }, [theme]);

    // External
    React.useEffect(() => {
        addExternalFnDirect("setTheme", (text: string): void => {
            const theme = text.includes("theme-") ? text : `theme-${text}`;
            setTheme(theme as Theme);
        });
    }, []);

    return { theme, setTheme };
};
